* {
    box-sizing:border-box;
}
body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #eff3f7;
    height: 100%;

}
html, 
body,
#potato,
.container{
    height: 100%;
    display: flex;
    justify-content: center;
}
.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.movies {
    display: flex;
    align-content: flex-start;
    justify-content: space-between;
    flex-wrap: wrap;
    padding :50px;
    width: 80%;
    padding-top: 70px;
    
}

.movies .movie{
    width: 55%;
    background-color: white;
    margin-bottom: 40px;
    margin: 100px -90px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding:20px;
    border-radius:6px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50,50, 93, 0.25),
        0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);
}
.movie img{
    position: relative;
    top:-50px;
    max-width: 180px;
    width:100%;
    margin-right:30px;
    box-shadow:  0 13px 27px -5px rgba(50,50, 93, 0.25),
    0 8px 16px -8px rgba(0,0,0,0.3), 0 -12px 32px -12px rgba(0,0,0,0.025);
}
.movie .movie__title,
.movie .movie__year {
    margin:0;
    font-weight: 300;
}
.movie .movie__title{
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie__genres{
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin: 5px 0px;
}
.movie__genres li {
    margin-right: 10px;
    font-size: 14px;
}
